<template>
    <div class="page">
        <!--文章列表-->
<!--        <LeftPlan/>-->
        <div class="articles">
            <!--返回按钮-->
            <Turnbutton/>
            <h1 class="tittle">{{articleShow.title}}</h1>
            <!--发表时间-->
            <div></div>
            <article v-html="articleShow.article" class="markdown-body"></article>

            <el-backtop target=".page"></el-backtop>

        </div>

    </div>

</template>

<script>
    import {getArticle} from "../../service/api";
    import Turnbutton from "../../components/button/Turnbutton";
    import LeftPlan from "./leftPlan/LeftPlan";

    export default {
        name: "Articles",
        components: {
            LeftPlan,
            Turnbutton
        },
        props: ["id"],
        data() {
            return {
                articleShow: []
            };
        },
        created() {
            let params = {
                id: this.id
            };
            //请求网络数据
            getArticle(params)
                .then(response => {
                    this.articleShow = response.data;
                })
                .catch(error => {
                    console.log(error);
                });
        }
    };

</script>

<style lang="scss" scoped>
    @import "../../style/statement";
    @import "../../style/markdown.css";

    .page {
        width: 100%;
        height: 100%;
        overflow-y: scroll;
    }

    .articles {
        font-size: 16px;
        min-width: 200px;
        max-width: 980px;
        margin: 0 auto;
        padding: 20px;
        border: 1px dashed black;
    }

    .tittle {
        font-size: 40px;
        margin: 0 auto;
        padding: 10px 0;
        border-bottom: #8c939d 3px solid;
    }

    .markdown-body {
        box-sizing: border-box;
    }

    @include styleAllPhone {
        .tittle {
            font-size: 25px;
        }

    }

</style>